@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'LCD14';
  src: local('LCD14'), url(/shot-timer/static/media/LCD14.c52a6a7e.otf) format('opentype');
}


html, body {
  margin: 0;
  padding: 0;
  min-width: 100%;
  min-height: 100%;
}

body, input, select, button {
  font-family: 'Poppins', sans-serif;
}

body, input, select {
  font-size: 19px;
  color: #333;
}

input, select {
  max-width: 100%;
  width: 100%;
}

* {
  box-sizing: border-box;
}

select, input {
  padding: 13px 15px;
  outline: none;
  border: 1px solid #aaa;
}

.btn {
  background: #ff0088;
  border: 0;
  padding: 9px 25px;
  font-size: 19px;
  color: white;
  outline: none;
  border-radius: 2px;
}

.btn:hover:focus {
  background: #d1006f;

}
.volume-display {
    width: 80%;
    max-width: 400px;
    position: relative;
    background: #eee;
    display: inline-block;
}

.volume-display .bar {
    background: green;
    height: 20px;
}

.volume-display .peak-indicator {
    position: absolute;
    bottom: 0;
    left: 0;

    background: black;
    width: 5px;
    height: 5px;
    z-index: 6;
}

.volume-display .threshold-indicator {
    position: absolute;
    height: 20px;
    background: rgba(200,200,200,0.5);
    border-right: 1px solid black;
    left: 0;
    top: 0;
    bottom: 0;
    z-index: 5;
}
.stage-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
}

.stage-inner {
    align-self: center;
    text-align: center;
    padding: 15px;
    width: 400px;
    max-width: 100%;
}
#stage-privileges .btn {
    margin-top: 30px;
    padding-left: 45px;
    padding-right: 45px;
}

input[type="range"] {
    width: 400px;
}
#stage-timer {
    padding: 40px 20px 80px;
}

#stage-timer .stage-inner {
    align-self: baseline;
}


.timer-display {
    font-size: 24px;
    background: #ddd;
    font-family: LCD14;
    padding: 20px 40px;
}
.timer-buttons {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
}

.timer-button {
    width: 100%;
    padding: 20px;
    font-size: 24px;
    color: white;
}

.timer-button-start {
    background-color: green;
}

.timer-button-standby {
    background-color: #d5ce00;
}

.timer-button-stop {
    background-color: red;
}
.timer-shots-list {
    padding: 20px 0;
}    

.timer-shots-list ul {
    list-style: none;
    padding: 0;
}

.timer-shots-list ul li {
    font-family: LCD14;
}
