@import url('https://fonts.googleapis.com/css?family=Poppins:300,400,500,700&display=swap');

@font-face {
  font-family: 'LCD14';
  src: local('LCD14'), url(../fonts/LCD14.otf) format('opentype');
}


html, body {
  margin: 0;
  padding: 0;
  min-width: 100%;
  min-height: 100%;
}

body, input, select, button {
  font-family: 'Poppins', sans-serif;
}

body, input, select {
  font-size: 19px;
  color: #333;
}

input, select {
  max-width: 100%;
  width: 100%;
}

* {
  box-sizing: border-box;
}

select, input {
  padding: 13px 15px;
  outline: none;
  border: 1px solid #aaa;
}

.btn {
  background: #ff0088;
  border: 0;
  padding: 9px 25px;
  font-size: 19px;
  color: white;
  outline: none;
  border-radius: 2px;
}

.btn:hover:focus {
  background: #d1006f;

}